* {
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
body {
    font-family: 'Fira Code', monospace;
    color: white;
    background-color: black;

}

#sideBarButton{
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
    -o-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
    position: absolute;
    right: var(--arrowButton-position, 63px);
    margin-top: 75px;
    height: 30px;
    width: 20px;
    background: rgb(36, 36, 36);
    border-radius: 3px;
    box-shadow: inset 0 0 0 1px rgb(54, 54, 54);
}
#arrow{
    position: absolute;
    top: -88px;
    left: var(--arrow-position, -92px);
    scale: 0.05;
    rotate: var(--arrow-direction,135deg);
}
#arrow:hover{
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
}


#sideBar {
    display: inline-block;
    visibility: var(--sidebar-vis, visible);
    position: absolute;
    opacity: var(--sidebar-opacity, 1);
    right: 0px;
    margin-top: -10px;
    width: 75px;
    height: 350px;
    background: rgb(36, 36, 36);
    scale: 0.8;
    border-radius: 10px;
    box-shadow: inset 0 0 0 2px rgb(54, 54, 54)
}

.anim1{
    animation-iteration-count: 1;
    animation-duration: 1s;
    animation-name: header-anim;
}
a {
display: inline-block;
}

#button1 {
    display: inline-block;
    opacity: var(--sidebar-opacity, 1);
    visibility: var(--sidebar-vis, visible);
    position: absolute;
    width: 50px;
    height: 50px;
    background: rgb(74, 74, 74);
    border-radius: 10px;
    right: 12px;
    margin-top: 17px;
    cursor: pointer;
}
.vkimg{
    width: 40px;
    height: 25px;
    margin-left: 5px;
    margin-top: 10px;
    margin-bottom: 20px;
}
#button1:hover{
    box-shadow: 
    1px 1px #000000,
    2px 2px #000000;
    -webkit-transform: translateX(-2px);
    transform: translateX(-2px);
}

#button2{
    display: inline-block;
    visibility: var(--sidebar-vis, visible);
    opacity: var(--sidebar-opacity, 1);
    position: absolute;
    width: 50px;
    height: 50px;
    background: rgb(74, 74, 74);
    border-radius: 10px;
    right: 12px;
    margin-top: 112px;
    cursor: pointer;
}
.dsimg{
    width: 40px;
    height: 40px;
    margin-top: 5px;
    margin-left: 5px;
    opacity: 0.6;
}
#icon2{
    margin-top: -59px;
    margin-left: 60px;
}
#button2:hover{
    box-shadow: 
    1px 1px #000000,
    2px 2px #000000;
    -webkit-transform: translateX(-2px);
    transform: translateX(-2px);
}

@keyframes header-anim{
    0%{
        opacity: 0;
    }
    10%{
        opacity: 0.1;
    }
    20%{
        opacity: 0.2;
    }
    30%{
        opacity: 0.3;
    }
    40%{
        opacity: 0.4;
    }
    50%{
        opacity: 0.5;
    }
    60%{
        opacity: 0.6;
    }
    70%{
        opacity: 0.7;
    }
    80%{
        opacity: 0.8;
    }
    90%{
        opacity: 0.9;
    }
    100%{
        opacity: 1;
    }
}

